body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.user-details {
  padding: 20px;
  margin: 0 0 20px 0;
}

.user-name {
  margin: 0 0 20px 0;
  font-size: 22px;
}

.user-coins-count {
  margin: 10px 0 0 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.user-coins-rate {
  margin: 10px 0 0 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.user-coins-count img, .user-coins-rate img {
  margin: 0 5px 0 0;
}


.chart {
  margin: 20px 0;
}

.chart-name {
  margin: 5px 0;
}

.chart-desc {
  font-size: 0.8rem;
}
.statistic-chart:not(:last-child) {
  margin: 0 0 20px 0;
  border-bottom: 1px solid #f4f5f7;
}
.contact-preview {
  display: inline-block;
  padding: 0 25px;
  height: 50px;
  width: 240px;
  font-size: 17px;
  line-height: 50px;
  border-radius: 25px;
  background-color: #f1f1f1; 
}
  
.contact-preview img {
  float: left;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%; 
}

.contacts-list-item {
  margin: 10px 0; 
}

.contacts-page {
  position: relative;
  height: 100%;
}

.contacts-page .search-container {
  /* height: 20px; */
  /* padding: 0 10px; */
  margin-left: 30px;
}

.contacts-page .contacts-container {
  max-height: 360px;
  overflow-x: auto;
}

.contacts-page .action-container {
  position: absolute;
  bottom: -3px;
  right: 10px;
}
.contact-details-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 40px;
}

.contact-details-header a:not(:last-child) {
  margin: 0 10px 0 0;
}

.contact-details .contact-details-row {
  margin: 10px;
}

.moves-list-container, .transter-coins-container {
  margin: 20px 0;
}

.contact-details-btns {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.contact-details-wrapper {
  max-width: 300px;
  padding: 0 20px;
  margin: 0 auto;
}
.contact-edit-rows {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.contact-edit-rows .contact-edit-row {
    margin: 10px;
}

.contact-edit-wrapper {
    max-width: 300px;
    padding: 0 20px;
    margin: 0 auto;
  }
.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

#Nav_menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 100%;
    height: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.Nav_link:link {
    color: black;
    font-size: 1rem;
    text-decoration: none;
    margin: 10px;
}

.Nav_link:hover {
